/*
  StatusList Component:
  This component displays a list of status fetched from the API. It provides the ability to add, update, and delete cities.
  It utilizes Chakra UI components for the table layout and styling.

  It does the following:
  - Imports necessary dependencies and components
  - Defines the interface for status lists
  - Retrieves the statuses and loading state from Redux using the useSelector hook
  - Initializes the dispatch function from Redux using the useDispatch hook
  - Initializes the navigate function from react-router-dom for navigation purposes
  - Defines the endpoint for API calls
  - Initializes the useToast hook for displaying toast messages
  - Fetches status data when the component mounts, only if there are no statuses present
  - Handles the delete operation for a status by dispatching the deleteData action and fetching updated status data
  - Renders the status list table using Chakra UI components, including table headers and rows
  - Displays a loader while data is being fetched
  - Provides buttons for adding a new status, refreshing the status list, and deleting a status with a confirmation dialog
*/
import React, { useEffect, useMemo } from "react";
import { useToast } from "@chakra-ui/react";

import {
  Box,
  Button,
  Divider,
  Spacer,
  Stack,
  Table,
  TableContainer,
  Tbody,
  Td,
  Th,
  Thead,
  Tr,
} from "@chakra-ui/react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { deleteData, getData } from "../../redux/actions/common.action";
import AlertDialogDelete from "../../components/AlertDialouge";
import { CiEdit } from "react-icons/ci";
import Loader from "../../components/Loader";
import { FiRefreshCcw } from "react-icons/fi";

//interface for the status lists
interface StatusLists {
  loading: boolean;
  statuses: {
    serialNumber: number;
    createdAt: string;
    StatusName: string;

    _id: number;
  }[];
}
const StatusList: React.FC = () => {
  // Accessing the statuses and loading state from the common reducer using the useSelector hook
  const { statuses, loading } = useSelector(
    (state: { common: StatusLists }) => state.common
  );

  // Obtaining the dispatch function using the useDispatch hook
  const dispatch: any = useDispatch();

  // Accessing the useNavigate hook and assigning it to the Navigate variable
  const Navigate = useNavigate();

  // Setting the endpoint to "status"
  const endpoint: string = "status";

  // Memoizing the statuses array using the useMemo hook
  const memoizedBranches = useMemo(() => statuses, [statuses]);

  // Fetching the data if the memoized statuses array is empty
  useEffect(() => {
    if (memoizedBranches.length === 0) {
      dispatch(getData(endpoint));
    }
  }, [dispatch, endpoint, memoizedBranches.length]);

  // Function to handle deletion of a status
  const handleDelete = async (id: number, toast: any) => {
    // Dispatching the deleteData action to delete the status with the specified ID
    await dispatch(deleteData(id, endpoint, toast));
    // Dispatching the getData action to fetch the updated status data
    dispatch(getData(endpoint));
  };

  const toast = useToast();

  return (
    <Stack h="100%" mt={1}>
      <Box
        px={10}
        h="40px"
        display={"flex"}
        justifyContent={"space-between"}
        alignItems={"center"}
      >
        {" "}
        {/* <Text color={"#000000"} fontSize={"1.25rem"} fontWeight={"600"}>
          Status List
        </Text> */}
        <Spacer />
        <Button
          onClick={() => Navigate("/add-status")}
          mt={2}
          size={"sm"}
          _hover={{ bg: "#FF9000" }}
          w="7.188rem"
          bg="#FF9000"
          borderRadius={"0.25rem"}
          color="whiteAlpha.900"
          fontWeight={"700"}
          fontSize={"1rem"}
        >
          {" "}
          Add Status
        </Button>
        <Box mt="2" ml="3">
          <Button
            size={"xs"}
            borderRadius={"full"}
            onClick={() => dispatch(getData(endpoint))}
          >
            <FiRefreshCcw />
          </Button>
        </Box>
      </Box>
      <Divider
        bg="gray.300" // Set the background color to dark gray
        height="2px" // Set the height of the divider
      />
      <TableContainer px="2">
        <Box overflowY="auto" height={480} maxHeight="480px">
          <Table variant="simple" size="sm">
            <Thead bg={"gray.100"} position={"sticky"} top="0">
              <Tr>
                <Th>#</Th>
                <Th>Date Created</Th>
                <Th>Status Name</Th>

                <Th>Action</Th>
              </Tr>
            </Thead>
            {loading ? (
              <Loader />
            ) : (
              <Tbody>
                {statuses?.map((el, i) => (
                  <Tr key={el._id}>
                    <Td>{i + 1}</Td>
                    <Td>{new Date(el.createdAt).toLocaleDateString()}</Td>
                    <Td>{el.StatusName}</Td>

                    <Td display={"flex"} alignItems={"center"}>
                      <Box
                        cursor={"pointer"}
                        mr={"5"}
                        onClick={() => Navigate(`/update-status/${el._id}`)}
                      >
                        <CiEdit color="blue" size={19} />
                      </Box>
                      <Box cursor={"pointer"}>
                        <AlertDialogDelete
                          id={el._id}
                          handleDelete={() => handleDelete(el._id, toast)}
                        />
                      </Box>
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            )}
          </Table>
        </Box>
      </TableContainer>
    </Stack>
  );
};

export default StatusList;
